.header{background-color:#222;border-bottom:1px dashed #fff}.header ul{display:grid;grid-template-columns:32px repeat(3,auto);list-style-type:none;margin:0;padding:10px 0}.header ul li{display:block;justify-self:center}.header a{color:#fff;text-decoration:none}.header a:hover{text-decoration:underline}.header ul li.city41Home{justify-self:start}.header ul li.city41Home img{height:200%;-ms-interpolation-mode:nearest-neighbor;image-rendering:-moz-crisp-edges;image-rendering:pixelated;width:200%}